import { mapMutations, mapGetters } from 'vuex';
export default {
	name: 'surveyAccount',
	components: {
		// Confirm
	},
	data() {
		return {
			questionImg:  this.Static+'images/business/home/extendManageList/question.png',
			UserData: [],
			arr: [],
			availableAmount: '', // 可用余额
			confirmShow: false,
			confirmBtn: '知道了',
			confirmTitle: '什么是可用余额？',
			confirmText: '可用余额：即账户余额减去冻结金额后的余额。'
		};
	},
	created() {
		// if (this.EVN === 'production') {
		//   axios.defaults.baseURL = this.apiProd2;
		// }
		this.getUserData();
	},
	methods: {
		...mapMutations({
			setLoadingShow: 'setLoadingShow',
			setLoadingText: 'setLoadingText'
		}),
		getUserData() {
			console.log('获取账户中心，账户数据');
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/compAccount/'+landRegistLG.user.id, //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
					},
					success: response => {
						uni.hideLoading();
						console.log(response.data);
						this.arr = response.data.content;
						console.log(this.arr);
					},
					fail: error => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		question() {
			uni.showModal({
				title: this.confirmTitle,
				content: this.confirmText,
				showCancel: false, // 不显示取消按钮
				confirmText: this.confirmBtn, // 确认按钮显示具体文字
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
					}
				}
			});
		},
	}
};
